// Add explicit console logging for debugging
console.info('InvenTree Card: Starting initialization');

import { LitElement, html, css } from "lit";
import { customElement, property } from "lit/decorators.js";
import { HomeAssistant, LovelaceCard, LovelaceCardConfig } from "custom-card-helpers";
import { InventreeCardConfig } from "./types";

console.info('InvenTree Card: Imports loaded');

// Use the decorator
@customElement('inventree-card')
export class InventreeCard extends LitElement implements LovelaceCard {
  @property({ attribute: false }) 
  public hass!: HomeAssistant;

  @property() 
  private config!: InventreeCardConfig;

  constructor() {
    super();
    console.info('InvenTree Card: Constructor called');
  }
  public static getStubConfig(): object {
    return {
      type: "custom:inventree-card",
      entity: "",
      show_low_stock: true,
      show_minimum: true,
      columns: 3
    };
  }

  setConfig(config: InventreeCardConfig) {
    if (!config.entity) {
      throw new Error("Please define an entity");
    }
    this.config = {
      show_low_stock: true,
      show_minimum: true,
      columns: 3,
      ...config
    };
  }

  getCardSize(): number {
    return 3;
  }

  protected render() {
    if (!this.hass || !this.config || !this.config.entity) {
      return html`
        <ha-card>
          <div class="card-content">
            Configuration error
          </div>
        </ha-card>
      `;
    }

    // Type-safe state access
    const state = this.config.entity in this.hass.states 
      ? this.hass.states[this.config.entity] 
      : undefined;

    if (!state) {
      return html`
        <ha-card>
          <div class="card-content">
            Entity ${this.config.entity} not found.
          </div>
        </ha-card>
      `;
    }

    const items = state.attributes?.items || [];
    const categoryName = state.attributes?.category_name;

    return html`
      <ha-card>
        <div class="card-header">
          <div class="name">${categoryName || 'Inventory'}</div>
          <div class="total">Total Stock: ${state.state}</div>
        </div>
        <div class="card-content">
          <div class="category-items">
            ${items.map((item: any) => this._renderItem(item))}
          </div>
        </div>
      </ha-card>
    `;
  }

  private _renderItem(item: any) {
    const isLowStock = Number(item.in_stock) <= Number(item.minimum_stock);
    
    return html`
      <div class="item-card ${isLowStock && this.config.show_low_stock ? 'low-stock' : ''}">
        <div class="name">${item.name}</div>
        <div class="stock">In Stock: ${item.in_stock}</div>
        ${this.config.show_minimum ? html`
          <div class="minimum">Minimum: ${item.minimum_stock}</div>
        ` : ''}
        <div class="controls">
          <ha-icon-button
            .label=${"Decrease stock"}
            @click=${() => this._handleStockAdjust(item.name, -1)}
          >
            <ha-icon icon="mdi:minus"></ha-icon>
          </ha-icon-button>
          <ha-icon-button
            .label=${"Increase stock"}
            @click=${() => this._handleStockAdjust(item.name, 1)}
          >
            <ha-icon icon="mdi:plus"></ha-icon>
          </ha-icon-button>
        </div>
      </div>
    `;
  }

  private _handleStockAdjust(itemName: string, quantity: number) {
    if (this.hass) {
      this.hass.callService('inventree', 'adjust_stock', {
        name: itemName,
        quantity: quantity
      });
    }
  }

  static get styles() {
    return css`
      :host {
        --grid-columns: 3;
      }
      .card-header {
        padding: 16px 16px 0;
        display: flex;
        justify-content: space-between;
        align-items: center;
      }
      .card-header .name {
        font-size: 1.2em;
        font-weight: 500;
      }
      .category-items {
        display: grid;
        grid-template-columns: repeat(var(--grid-columns), 1fr);
        gap: 16px;
        padding: 16px;
      }
      .item-card {
        background: var(--card-background-color, var(--ha-card-background));
        border-radius: var(--ha-card-border-radius, 4px);
        box-shadow: var(--ha-card-box-shadow, none);
        padding: 16px;
        text-align: center;
      }
      .item-card.low-stock {
        border: 2px solid var(--error-color);
      }
    `;
  }
}
console.info('InvenTree Card: Class defined');
// Register with customCards
window.customCards = window.customCards || [];
window.customCards.push({
  type: "custom:inventree-card",
  name: "InvenTree Card",
  description: "Display and manage InvenTree inventory"
});

console.info('InvenTree Card: Registration complete');